// Colors
// ---------------------------------

// Default

$color-default-black: #041827;
$color-default-white: #ffffff;
$color-default-background: #f8fdff;
$color-transparent: rgba(255, 255, 255, 0);
$color-default-white-transparent: rgba(255, 255, 255, 0.7);


// Project palette
// для нейминга цветов используем https://www.htmlcsscolor.com/hex/334482

$color-whisper: #e5e5e5;
$color-alice-blue: #f2f5f6;
$color-deep-sky-blue: #00b2ff;
$color-sky-blue: #00b3ff;
$black-pearl: #041427;

// Gradient

$black-to-bottom: linear-gradient(to bottom, $color-default-black 84.9%, rgba(4, 24, 39, 0) 100%);
$gray-to-bottom: linear-gradient(to bottom, rgba(25, 32, 36, 0.7) 72%, rgba(25, 32, 36, 0) 100%);
$gray-transparent-cover: linear-gradient(to bottom, rgba(4, 24, 39, 0.5));

// Typography
// ---------------------------------

// переменная используемая в html для подключения скейлинга
$fz-default: 16px;

// у некоторых шрифтов в ios возникают проблемы с символом рубля
// https://www.artlebedev.ru/kovodstvo/sections/159/#13
$ff-rouble: "Rouble", "Arial", sans-serif;

$ff-placeholder: "Placeholder", "Arial", sans-serif;

$ff-basic: "PT Mono", "Consolas", monospace;
$ff-fancy: "Roboto Condensed", "Verdana", sans-serif;

// Animation
// ---------------------------------

$tf-default: ease;
$trans-default: 0.3s $tf-default;

// если требуется изинг отличный от $tf-default, то переменную называем не цифрой
$trans-modal: 0.6s cubic-bezier(0.55, 0, 0.1, 1);

$trans-600: 0.6s $tf-default;

// Viewports
// ---------------------------------

$vp-320: 320px;
$vp-375: 375px;
$vp-768: 768px;
$vp-1024: 1024px;
$vp-1280: 1280px;
$vp-1440: 1440px;
$vp-1920: 1920px;

// Retina
// ---------------------------------

$retina-dpi: 144dpi;
$retina-dppx: 2dppx;
